<template>
	<!-- 当mescroll-body写在子组件时,父页面需引入mescroll-comp.js的mixins -->
	<mescroll-body ref="mescrollRef" top="100" @init="mescrollInit" @down="downCallback" @up="upCallback">
		<!-- 数据列表 -->
		<view class="sr-scan-item" v-for="(item,index) in listData" :key="index">
			<view class="sr-scan-item-header">
				<text>扫码拉环</text>
				<text>{{item.message}}</text>
			</view>
			<view class="sr-scan-item-tiem">
				{{item.create_time}}
			</view>
		</view>
	</mescroll-body>
</template>

<script>
	import MescrollMixin from "@/components/mescroll-uni/mescroll-mixins.js";
	import {scanlog} from "@/api/homeApi.js"
	
	
	export default {
		mixins: [MescrollMixin], // 使用mixin (在main.js注册全局组件)
		data() {
			return {
				listData: [],
				select_time:[]
			}
		},
		methods: {
			/*上拉加载的回调: 其中page.num:当前页 从1开始, page.size:每页数据条数,默认10 */
			upCallback(page) {
				let parmas = {}
				//配置查询参数
				if(this.select_time&&this.select_time.length>0){
					parmas.start_time = this.select_time[0]
					parmas.end_time = this.select_time[1]
				}
				//联网加载数据
				scanlog(parmas).then(res=>{
					let data = res.data.list||[] 
					//联网成功的回调,隐藏下拉刷新和上拉加载的状态;
					this.mescroll.endSuccess(data.length);
					//设置列表数据
					if(page.num == 1) this.listData = []; //如果是第一页需手动制空列表
					this.listData=this.listData.concat(data); //追加新数据
				}).catch(()=>{
					//联网失败, 结束加载
					this.mescroll.endErr();
				})
			},
			select_data(time){
				this.select_time = time
				this.mescroll.resetUpScroll()
			}
		}
	}
</script>
<style lang="scss">
	.sr-scan-item{
		padding:40rpx;
		position: relative;
		&::after{
			content: '';
			position: absolute;
			bottom: 0;
			left: 50%;
			width: 80%;
			transform: translateX(-50%);
			border-bottom: 2rpx dashed #e2e2e2;
		}
		.sr-scan-item-header{
			display: flex;
			justify-content: space-between;
			font-size: 28rpx;
			color: #333333;
			font-weight: 700;
		}
		.sr-scan-item-tiem{
			margin-top: 10rpx;
			color: #999;
			font-size: 24rpx;
		}
	}
</style>
